body {
  background-color: #f8f8f8;
  font-family: $main-font;
  font-size: 9pt; }

#wrapper {
  width: 100%; }

#page-wrapper {
  padding: 0 10px;
  min-height: 528px;
  background-color: #fff; }

.navbar-brand {
  padding-top: 6px; }

.page-subject {
  font-size: 18pt;
  color: #fff;
  text-shadow: 1px 1px 2px #999;
  margin: 6px 0 0 10px;
  float: left; }

@media(min-width: 768px) {
  #page-wrapper {
    position: inherit;
    margin: 52px 0 0 250px;
    padding: 15px 30px;
    border-left: 1px solid #e7e7e7; } }

.page-header {
  margin: 10px 0 10px;
  font-size: 1.5em; }

.navbar-top-links {
  margin-right: 0; }

.navbar-top-links li {
  display: inline-block; }

.navbar-top-links li:last-child {
  margin-right: 15px; }

.navbar-top-links li a {
  padding: 15px;
  min-height: 50px; }

.navbar-top-links .dropdown-menu li {
  display: block; }

.navbar-top-links .dropdown-menu li:last-child {
  margin-right: 0; }

.navbar-top-links .dropdown-menu li a {
  padding: 3px 20px;
  min-height: 0; }

.navbar-top-links .dropdown-menu li a div {
  white-space: normal; }

.navbar-top-links .dropdown-messages,
.navbar-top-links .dropdown-tasks,
.navbar-top-links .dropdown-alerts {
  width: 310px;
  min-width: 0; }

.navbar-top-links .dropdown-messages {
  margin-left: 5px; }

.navbar-top-links .dropdown-tasks {
  margin-left: -59px; }

.navbar-top-links .dropdown-alerts {
  margin-left: -123px; }

.navbar-top-links .dropdown-user {
  right: 0;
  left: auto; }

.navbar-top-links li.active {
  background-color: #ddd; }

@media(max-width: 900px) {
  #page-wrapper {
    padding: 15px;
    margin-top: 100px !important; } }

.sidebar .sidebar-nav.navbar-collapse {
  padding-right: 0;
  padding-left: 0; }

.sidebar .sidebar-search {
  padding: 15px; }

.sidebar ul li {
  border-bottom: 1px solid #e7e7e7; }

.sidebar ul li a.active {
  background-color: #eee; }

.sidebar .arrow {
  float: right; }

.sidebar .fa.arrow:before {
  content: "\f104"; }

.sidebar .active > a > .fa.arrow:before {
  content: "\f107"; }

.sidebar .nav-second-level li,
.sidebar .nav-third-level li {
  border-bottom: 0 !important; }

.sidebar .nav-second-level li a {
  padding-left: 37px; }

.sidebar .nav-third-level li a {
  padding-left: 52px; }

@media(min-width: 768px) {
  .sidebar {
    z-index: 1;
    position: absolute;
    width: 250px;
    margin-top: 51px; }

  .navbar-top-links .dropdown-messages,
  .navbar-top-links .dropdown-tasks,
  .navbar-top-links .dropdown-alerts {
    margin-left: auto; } }

.btn-outline {
  color: inherit;
  background-color: transparent;
  transition: all .5s; }

.btn-primary.btn-outline {
  color: $primary-color; }

.btn-success.btn-outline {
  color: $success-color; }

.btn-info.btn-outline {
  color: $info-color; }

.btn-warning.btn-outline {
  color: $warning-color; }

.btn-danger.btn-outline {
  color: $danger-color; }

.btn-primary.btn-outline:hover,
.btn-success.btn-outline:hover,
.btn-info.btn-outline:hover,
.btn-warning.btn-outline:hover,
.btn-danger.btn-outline:hover {
  color: #fff; }

.chat {
  margin: 0;
  padding: 0;
  list-style: none; }

.chat li {
  margin-bottom: 10px;
  padding-bottom: 5px;
  border-bottom: 1px dotted #999; }

.chat li.left .chat-body {
  margin-left: 60px; }

.chat li.right .chat-body {
  margin-right: 60px; }

.chat li .chat-body p {
  margin: 0; }

.panel .slidedown .glyphicon,
.chat .glyphicon {
  margin-right: 5px; }

.chat-panel .panel-body {
  height: 350px;
  overflow-y: scroll; }

.btn-circle {
  width: 30px;
  height: 30px;
  padding: 6px 0;
  @include border-radius(15px);
  text-align: center;
  font-size: 12px;
  line-height: 1.428571429; }

.btn-circle.btn-lg {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  @include border-radius(25px);
  font-size: 18px;
  line-height: 1.33; }

.btn-circle.btn-xl {
  width: 70px;
  height: 70px;
  padding: 10px 16px;
  @include border-radius(35px);
  font-size: 24px;
  line-height: 1.33; }

.show-grid [class^=col-] {
  padding-top: 10px;
  padding-bottom: 10px;
  border: 1px solid #ddd;
  background-color: #eee !important; }

.show-grid {
  margin: 15px 0; }

.huge {
  font-size: 40px; }

.last_update_info {
  color: $gray-color;
  padding-bottom: 5px; }

.add-link {
  font-size: 10pt;
  text-shadow: none;
  text-decoration: underline;
  color: $success-color; }

.filter_bar {
  padding: 5px 10px;
  background-color: #f0f0f0;
  @include border-radius(3px); }

.search-row {
  margin-top: 5px; }

.input-inline {
  width: 100%;
  height: 34px;
  padding: 6px 12px;
  font-size: 14px;
  color: #555;
  background-color: #fff;
  border: 1px solid #ccc;
  @include border-radius(4px);
  @include box-shadow(inset 0 1px 1px rgba(0, 0, 0, .075)); }

.troubleshoot-box {
  background-color: #f7f7f7;
  border: 1px solid #eee;
  @include border-radius(5px);
  padding: 5px 10px;
  margin-bottom: 10px; }

.troubleshoot-box h4 {
  font-size: 1.3em;
  border-bottom: 1px solid #aaa; }

.language {
  padding-top: 5px; }

.copyright-bar {
  padding: 10px;
  font-size: .8em;
  border-top: 1px solid #eee; }

.discount_rate_tb thead th {
  border-bottom: 2px solid #eee;
  text-align: center; }

.discount_rate_tb tbody td {
  border-bottom: 1px dashed #eee;
  padding: 3px 2px; }

.background-gray {
  background-color: #ccc; }

@import "common";
